<template>
  <div class="DetailBlog">
      <h1>{{blog.title}}</h1>
      <article> {{blog.content}}</article>
      <p>作者:{{blog.author}}</p>
      <p>分类:</p>
      <ul>
        <li v-for="(category,index) in blog.categories" :key="index"> 
            {{category}}
        </li>
      </ul>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: "DetailBlog",
  data() {
    return {
        id:this.$route.params.id,
        blog:{}
    }; 
  },
  created(){
      axios.get('https://blog-c862c-default-rtdb.firebaseio.com/posts/'+this.id+'.json')
      .then((data)=>{
          this.blog = data.data
          console.log(this.blog)
      })
  }
};
</script>
<style scoped>
    .DetailBlog {
        max-width: 960px;
        margin: 0 auto;
        padding: 20px;
        background: #eee;
        /* background: #383841; */
        box-shadow: 1px 1px 5px rgba(255,255,255,.1);
    }
    h1,article,p,li {
      color: #fff;
    }

