<template>
  <div class="AddBlog">
    <h2>写博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>
      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>
      <div id="checkboxs">
          <label>Vue.js</label>
          <input type="checkbox" value="Vue.js" v-model="blog.categories">
          <label>React.js</label>
          <input type="checkbox" value="React.js" v-model="blog.categories">
          <label>Angular.js</label>
          <input type="checkbox" value="Angular.js" v-model="blog.categories">
          <label>Node.js</label>
          <input type="checkbox" value="Node.js" v-model="blog.categories">
      </div>
      <label>作者</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author.id">{{author.name}}</option>
      </select>
      <button @click.prevent="post">发表文章</button>
    </form>
    <div v-if="submmited">
      <h3>您的博客发送成功</h3>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "AddBlog",
  data() {
    return {
      blog:{
        title:"",
        content:"",
        categories:[],
        author:'Alice'
      },
      authors:[
        {id:0,name:'Jack'},
        {id:1,name:'Tom'},
        {id:2,name:'Alice'},
        {id:3,name:'Allen'},
      ],
      submmited:false
    };
  },
  methods:{
    post(){
      axios.post('https://blog-c862c-default-rtdb.firebaseio.com/posts.json',this.blog).then(({data})=>{
        //console.log(data)
        this.submmited = true
      })
    }
  }
};
</script>


