<template>
  <div class="ShowBlog">
    <input type="text" v-model="search" placeholder="搜索文章">
    <div v-for="blog in filterBlog" :key="blog.id" class="single-blog">
        <router-link :to="'/blog/'+blog.id"><h2 v-rainbow>{{blog.title | to-upperCase}}</h2></router-link>
        <article>
            {{blog.content | snippet}}
        </article>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "ShowBlog",
  data() {
    return {
      blogs: [],
      search:''
    };
  },
  created(){
    //let that = this
    axios.get('https://blog-c862c-default-rtdb.firebaseio.com/posts.json')
    .then((data)=>{
        var res = JSON.parse(JSON.stringify(data.data))
        //console.log(JSON.parse(JSON.stringify(data.data)))
        return res
    }).then((datas)=>{
        var blogsArray = []
        for(let key in datas){
            //console.log(datas[key])
            datas[key].id = key
            blogsArray.push(datas[key])
        }
        //console.log(blogsArray)
        this.blogs = blogsArray
    })
  },
  computed:{
      filterBlog:function(){
          return this.blogs.filter((item)=>{
              return item.title.match(this.search)
          })
      }
  },
  //局部过滤器
  filter:{
      'to-upperCase':function(value){
          return value.toUpperCase()
      }
  },
  //局部指令
  directives:{
      'rainbow':{
          bind(el,binding,vnode){
              //el.style.color = '#' + Math.random().toString(16).slice(2,8)
              el.style.color = '#fff'
          }
      }
  }
  
};
</script>
<style scoped>
    .ShowBlog {
        max-width: 800px;
        margin: 0 auto;
    }
    .single-blog {
        padding: 20px;
        margin: 20px 0;
        box-sizing: border-box;
        /* background: #383841; */
        box-shadow: 1px 1px 5px rgba(255,255,255,.1);
    }
    .ShowBlog a {
        text-decoration: none;
        color: #444;
    }
    input[type='text'] {
        padding: 8px;
        width: 100%;
        box-sizing: border-box;
        /* background: #383841; */
        color: #fff;
    }
    .article {
        color: #fff;
    }
</style>
